import React,{Component} from 'react';

class Home extends Component{
    render(){
        return(
            <>
            <section className="hero-section img-bg">
                <div className="container">
                    <div className="row">
                        <div className="col-xl-7 col-lg-6">
                            <div className="hero-content-wrapper mb-5">
                                <h1>Software and SaaS Landing Page Template</h1>
                                <p className="p">Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore.</p>
                                <a className="theme-btn">Explore Features</a>
                            </div>
                        </div>
                        <div className="col-xl-5 col-lg-6">
                            <div className="hero-img">
                                <img src={process.env.PUBLIC_URL+'/img/hero-img.png'}className="img-fluid" />
                                <img src={process.env.PUBLIC_URL+'/img/hero-shape.svg'} className="shape img-fluid"/>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            </>
        );
    }
}

export default Home;