import React ,{Component} from 'react';

class Counterup extends Component{
    render(){
        return(
            <>
                <section className="counterup-section pt-50 pb-50">
                    <div className="container">
                        <img src={process.env.PUBLIC_URL+'/img/repeat-shape.svg'} className="shape counter-shape-1" />
                        <img src={process.env.PUBLIC_URL+'/img/repeat-shape.svg'} className="shape counter-shape-2" />

                        <div className="counter-wrapper gradient-1 pt-50">
                            <div className="row">
                                <div className="col-xl-4 col-lg-4 col-md-4">
                                    <div className="single-counter text-center mb-50">
                                        
                                        <i className="far fa-heart icon-gradient gradient-2 mb-30"></i>
                                        
                                        <h3 className="text-white">125k</h3>
                                        <p className="mb-0">
                                            Happy Users
                                        </p>
                                    </div>
                                </div>
                                <div className="col-xl-4 col-lg-4 col-md-4">
                                    <div className="single-counter text-center mb-50">
                                        
                                        <i className="far fa-star icon-gradient gradient-3 mb-30"></i>
                                        
                                        <h3 className="text-white">45k</h3>
                                        <p className="mb-0">
                                            Great Reviews
                                        </p>
                                    </div>
                                </div>
                                <div className="col-xl-4 col-lg-4 col-md-4">
                                    <div className="single-counter text-center mb-50">
                                        
                                        <i className="fas fa-download icon-gradient gradient-4 mb-30"></i>
                                        
                                        <h3 className="text-white">305k</h3>
                                        <p className="mb-0">
                                            Downloads
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </>
        );
    }
}

export default Counterup;