import React ,{Component} from 'react';

class Footer extends Component{
    render(){
        return(
            <>
                <footer className="footer pt-150">
                    <div className="container">
                        <div className="row">
                            <div className="col-xl-12">
                                <div className="footer-links text-center">
                                    <a href="#" className="logo">
                                        <img src={process.env.PUBLIC_URL+'/img/logo-2.svg'} />
                                    </a>
                                    <div className="footer-social-links">
                                        <ul>
                                            <li>
                                                <a><i className="fab fa-facebook-f"></i></a>
                                            </li>
                                            <li>
                                                <a><i className="fab fa-twitter"></i></a>
                                            </li>
                                            <li>
                                                <a><i className="fab fa-linkedin-in"></i></a>
                                            </li>
                                            <li>
                                                <a><i className="fab fa-instagram"></i></a>
                                            </li>
                                        </ul>
                                    </div>
                                    <nav className="footer-menu">
                                        <ul>
                                            <li><a href="#home">Home</a></li>
                                            <li><a href="#service">Service</a></li>
                                            <li><a href="#process">Process</a></li>
                                            <li><a href="#team">Team</a></li>
                                            <li><a href="#blog">Blog</a></li>
                                            <li><a href="#contact">Contact</a></li>
                                        </ul>
                                    </nav>
                                </div>
                            </div>
                        </div>
                        <div className="copyright-area text-center">
                            <p className="mb-0">Designed and Developed by <a href="https://github.com/AhmadAlmuhammad" rel="nofollow" target="_blank">Ahmed Al-Mohammed</a></p>
                        </div>
                    </div>
                </footer>
            </>
        );
    }
}

export default Footer;