import React,{Component} from 'react';
import Header from './HeaderComponent';
import Home from './HomeComponent';
import Features from './FeturesComponent';
import Whyuse from './WhyuseComponent';
import Process from './ProcessComponent';
import Counterup from './CounterupComponent';
import Team from './TeamComponent';
import Testimonial from './TestimonialComponent';
import Footer from './FooterComponent';
class Main extends Component{
    render(){
        return(
            <>
            <Header />
            <Home />
            <Features />
            <Whyuse />
            <Process />
            <Counterup />
            <Team />
            <Testimonial />
            <Footer />
            </>
        );
    }
}

export default Main;