import React,{Component} from 'react';
class Header extends Component{
    render(){
        return(
            <>
            <header className="header">
                <div className="navbar-area">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <nav className="navbar navbar-expand-lg">
                                    <a className="navbar-brand">
                                        
                                            <img src={process.env.PUBLIC_URL+'/img/logo-2.svg'} width="150" className="img-2"/>
                                            <img src={process.env.PUBLIC_URL+'/img/logo.svg'} width="150" className="img-1"/>
                                        
                                    </a>
                                    <button class="navbar-toggler collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                                        <span class="toggler-icon"></span>
                                        <span class="toggler-icon"></span>
                                        <span class="toggler-icon"></span>
                                    </button>
                                    <div class="navbar-collapse sub-menu-bar collapse" id="navbarSupportedContent">
                                        <ul id="nav" class="navbar-nav ml-auto">
                                            <li class="nav-item">
                                                <a class="nav-link" href="#home">Home</a>
                                            </li>
                                            <li class="nav-item">
                                                <a class="nav-link" href="#features">Features</a>
                                            </li>
                                            <li class="nav-item">
                                                <a class="nav-link" href="#process">Process</a>
                                            </li>
                                            <li class="nav-item">
                                                <a class="nav-link" href="#team">Team</a>
                                            </li>
                                            <li class="nav-item">
                                                <a class="nav-link" href="#blog">Blog</a>
                                            </li>
                                            <li class="nav-item">
                                                <a class="nav-link" href="#subscribe">Subscribe</a>
                                            </li>
                                        </ul>
                                    </div>
                                </nav>
                            </div>
                        </div>
                    </div>
                </div>
            </header>
            </>
        );
    }
}

export default Header;